#infoContent {
  grid-area: content;
  max-height: 100%; }
  #infoContent img {
    margin: 10px 0px 0px 40px; }
  #infoContent p {
    color: #bbb;
    margin: 5px 0px 5px 40px;
    width: 80%; }
  #infoContent .defaut_value {
    margin-left: 50px;
    color: #afd879; }
  #infoContent .h3_explanation {
    margin-left: 30px; }
  #infoContent .comment-italic {
    color: grey;
    font-size: 13px;
    margin-left: 0px;
    font-style: italic; }
